<template>
	<div>
		<h1>扫码测试结果</h1>
		<div>
			OpenId:{{openId}}
		</div>
		<div>
			access_token:{{access_token}}
		</div>
		<div>
			md5:{{md5}}
		</div>
		<input type="button" value="获取数据" @click="abcd">

		<div>
			objectId:{{objectId}}
		</div>
		<div>
			projectId:{{projectId}}
		</div>
		<div>
			types:{{types}}
		</div>
	</div>
</template>

<script>
	import tools from 'utils/tools'
	import {
		getVerifyMes
	} from 'api/global'
	import {
		getAccessToken
	} from 'api/other'
	import {
		getObjectInfoByMd5
	} from 'api/repair'
	export default {
		name: 'smceshi',
		data: function() {
			return {
				openId: '',
				access_token: '',
				md5: '',
				objectId: '',
				projectId: '',
				types: ''
			}
		},
		methods: {
			abcd() {
				getObjectInfoByMd5(this.md5).then(result => {
					this.objectId = result.data.objectId
					this.projectId = result.data.projectId
					this.types = result.data.type
				})
			}
		},
		beforeRouteEnter: (to, from, next) => {
			let usrMess = {
				openId: tools.getCookie('openId')
			}
			getAccessToken().then(token => {
				if(token.data.Result === 'success') {
					usrMess.access_token = token.data.acesstoken
					return getVerifyMes(usrMess)
				}
			}).then(result => {
				const userstatus = result.data.subscribe
				if(userstatus == 1) {
					//说明用户关注了
					next(vm => {
						vm.md5 = to.query.md5
						vm.openId = usrMess.openId
						vm.access_token = usrMess.access_token
					})
				} else {
					//用户没有关注
					// const locationHref = 'http://weixin.qq.com/r/WUQqMobEpNvbrcJR9xFL';
					// window.location.replace(locationHref)
					next(vm => {
						vm.$router.replace('ydgz')
					})
				}
			})
		}
	}
</script>

<style scoped>

</style>


